import { Component, ContentChildren, QueryList, AfterContentInit, Output, EventEmitter } from '@angular/core';
import {TabComponent as Tab} from '../Tab/tab.component'

@Component({
  selector: 'tabs',
  templateUrl: 'tabs.component.html',
  styleUrls:['tabs.component.scss']
})
export class Tabs implements AfterContentInit {

  @Output() onTabChange: EventEmitter<Tab> = new EventEmitter<Tab>();
  @ContentChildren(Tab) tabs: QueryList<Tab>;

  // contentChildren are set
  ngAfterContentInit() {
    // get all active tabs
    let activeTabs = this.tabs.filter((tab)=>tab.active);

    // if there is no active tab set, activate the first
    if(activeTabs.length === 0) {
      this.selectTab(this.tabs.first);
    }
  }

  selectTab(tab: Tab){
    // deactivate all tabs
    this.tabs.toArray().forEach(tab => tab.active = false);

    // activate the tab the user has clicked on.
    tab.active = true;
    this.onTabChange.emit(tab);
  }

}




