/**
 * Created by Venkatesh on 3/2/2017.
 * As per the best practices moved routing to separate module
 */

import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { RouterModule }  from '@angular/router';
import { DATASTOREComponent } from "./index";
import {GLOBALModule} from "../global/global.module";

const dataStoreRoutes=[
  { path:'datastore',component:DATASTOREComponent }
]
@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(dataStoreRoutes),
    GLOBALModule
  ],
  exports: [
    RouterModule
  ]
})
export class DataStoreRoutingModule {}



