import {ServiceProvider} from "../../models/index";
import {ProgressIndicator} from "../../components/ProgressIndicator/progressindicator.component";

interface  DataCenterConfigInterface {
  deviceServiceProvider: ServiceProvider;
  volumeServiceProvider: ServiceProvider;
}

export const DataCenterConfig: DataCenterConfigInterface = {
  deviceServiceProvider: {
    url: '/api/getStorageLst',
    type: 'POST',
    contentType: 'application/json'
  },
  volumeServiceProvider: {
    url: '/api/getVolumeMappingLst',
    type: 'POST',
    contentType: 'application/json'
  }
};

export const deviceColumnDefs = [
  {headerName: "Device Name", field: "deviceName"},
  {headerName: "State", field: "state"},
  {headerName: "Error Events", field: "errEvents"}, {
    headerName: "Capacity",
    field: "capacity",
    cellRendererFramework: ProgressIndicator
  },
  {
    headerName: "Connected Host",
    field: "host",
    width: 100,
    cellStyle: {'padding': '0px', 'height': '45px', 'text-align': 'center'}
  }
];

export const volumeColumnDefs = [
  {headerName: "Name", field: "name"},
  {headerName: "Size", field: "size"},
  {
    headerName: "Capacity",
    field: "capacity",
    cellRendererFramework: ProgressIndicator,
    width: 350
  },
  {headerName: "NAA", field: "naa"},
  {headerName: "DataStore", field: "dataStore"},
  {headerName: "Dedup", field: "dedup"}, {
    headerName: "Host",
    field: "host",
    width: 100
  },
  {headerName: "Performance", field: "performance"}
];



