import {Component, OnInit, AfterViewInit,} from '@angular/core';
import {GridOptions} from "ag-grid";
import {DataStoreConfig, deviceColumnDefs, volumeColumnDefs} from './datastore.component.config';
import {DataCenterService, AuthenticationService} from "../../services/index";
//TODO remove after integrating with service
import {_tmpMultiAreaChartDatafn} from '../../models/index'
import {DateUtil} from "../../services/date-util.service";

@Component({
  selector: 'datastore-component',
  styleUrls: ['./datastore.component.scss'],
  templateUrl: './datastore.components.html',
  providers: [DataCenterService,DateUtil]
})

export class DATASTOREComponent implements OnInit, AfterViewInit {
  private _timeStamp: any = '';
  private deviceGridOptions: GridOptions;
  private volumeGridOptions: GridOptions;
  private showPerformanceDialog: boolean = false;
  private showHostDialog: boolean = false;
  private _tmpAreaChartData = _tmpMultiAreaChartDatafn();
  private showDataStoreWizard: boolean = false;
  private deviceLstHeight = 250;
  private volumeLstHeight = 250;

  constructor(private auth: AuthenticationService, private service: DataCenterService,private dateUtil:DateUtil) {
  }

  ngAfterViewInit() {
    console.log(decodeURIComponent(window['WEB_PLATFORM'].getObjectId()));
  }

  onDeviceLstCellClicked(event) {
    console.log(event);
    if (event.colDef.field == "performance") {
      this.showPerformanceDialog = true;
      this._tmpAreaChartData = _tmpMultiAreaChartDatafn();
    }
    else if(event.colDef.field == "host") {
      this.showHostDialog = true;
    }
  }

  addDataStoreDetails() {
    this.showDataStoreWizard = true;
  }

  navToWizard() {
    console.log("Inside navToWizard");
  }

  previousStep() {
    console.log("Inside previousStep");
  }

  wizardNextStep() {
    console.log("Inside wizardNextStep");
  }

  closeDataStr(flag) {
    this.showDataStoreWizard = flag;
  }

  closePerformanceDialog(shodeClose) {
    this.showPerformanceDialog = false;
  }

  hideConnectedHost() {
    this.showHostDialog = false;
  }

  ngOnInit() {
    this._timeStamp = this.dateUtil.getCurrentDateAndTime();
    DataStoreConfig.deviceServiceProvider.url = DataStoreConfig.deviceServiceProvider.url.replace('$expression', (decodeURIComponent(window['WEB_PLATFORM'].getObjectId()).split(":")[3] || false) + '');
    this.deviceGridOptions = <GridOptions>{
      columnDefs: deviceColumnDefs,
      rowData: null,
      rowSelection: 'multiple',
      enableSorting: true,
      suppressRowClickSelection: true,
      enableColResize: true,
      minRowsToShow: 1,
      rowHeight: 35,
      onGridReady: () => {
        this.deviceGridOptions.api.sizeColumnsToFit();
      }
    };
    this.volumeGridOptions = <GridOptions>{
      columnDefs: volumeColumnDefs,
      rowData: null,
      rowSelection: 'multiple',
      suppressRowClickSelection: true,
      enableColResize: true,
      minRowsToShow: 1,
      enableSorting: true,
      rowHeight: 35,
      onGridReady: () => {
        this.volumeGridOptions.api.sizeColumnsToFit();
      }
    };
    this.loadDeviceLst();
  }
  loadDeviceLst() {
    this.service.getDeviceLst(this.auth.getUserProfile(), DataStoreConfig.deviceServiceProvider)
      .subscribe(
        data => {
          if(data){
            this.deviceGridOptions.rowData =[data[0]];
            if(data[0].storage && data[0].storage.vol)
              this.volumeGridOptions.rowData = [data[0].storage.vol];
            else
              this.volumeGridOptions.rowData = [];
          }

          if (data.length  > 0)
            this.deviceLstHeight = ((data.length ) * this.deviceGridOptions.rowHeight) < this.deviceLstHeight ? ((data.length + 1) * this.deviceGridOptions.rowHeight) : this.deviceLstHeight;

          if (this.volumeGridOptions.rowData.length > 0)
            this.volumeLstHeight = ((this.volumeGridOptions.rowData.length) * this.volumeGridOptions.rowHeight) < this.volumeLstHeight ? ((this.volumeGridOptions.rowData.length + 1) * this.volumeGridOptions.rowHeight) : this.volumeLstHeight;

        },
        err => {
          this.deviceGridOptions.rowData = [];
          this.volumeGridOptions.rowData = [];
          console.log(err);
        }
      );
  }

  loadVolumeInfo(data) {
    if(data.length > 0) {
      this.volumeGridOptions.rowData = data[0];
    }

    let rowLen = this.volumeGridOptions.rowData && this.volumeGridOptions.rowData.length ?  this.volumeGridOptions.rowData.length  :  1;
    if (rowLen > 0)
      this.volumeLstHeight = ((rowLen) * this.volumeGridOptions.rowHeight) < this.volumeLstHeight ? ((rowLen + 1) * this.volumeGridOptions.rowHeight) : this.volumeLstHeight;
  }
}



