export let _tmpPieChartDatafn = function () {
  return [
    {
      key: 'free',
      value: (Math.floor(Math.random() * (100 - 10 + 1)) + 10)
    },
    {
      key: 'used',
      value: (Math.floor(Math.random() * (100 - 10 + 1)) + 10)
    }
  ]
};

export let _tmpMultiAreaChartDatafn = function () {
  return [
    {
      "key": "Throughout [Mbps]",
      "values": (function () {
        let _r = [], i = 35;
        while (i--) {
          _r.push([1490084062709 - (i * 10000), (Math.floor(Math.random() * (100 - 10 + 1)) + 10)])
        }
        return _r;
      })()
    },
    {
      "key": "IOPs [Mbps]",
      "values": (function () {
        let _r = [], i = 35;
        while (i--) {
          _r.push([1490084062709 - (i * 10000), (Math.floor(Math.random() * (100 - 10 + 1)) + 10)])
        }
        return _r;
      })()
    },

    {
      "key": "Latency ",
      "values": (function () {
        let _r = [], i = 35;
        while (i--) {
          _r.push([1490084062709 - (i * 10000), (Math.floor(Math.random() * (100 - 10 + 1)) + 10)])
        }
        return _r;
      })()
    }
  ]
};

export let _tmpAreaChartDatafn = function (lbl) {
  return [
    {
      "key": lbl,
      "values": (function () {
        let _r = [], i = 35;
        while (i--) {
          _r.push([1490084062709 - (i * 10000), (Math.floor(Math.random() * (100 - 10 + 1)) + 10)])
        }
        return _r;
      })()
    }];
}

export let _tmpMultiBarChartDatafn = function () {
  return [
    {
      "key": "Series1",
      "color": "#d62728",
      "values": [
        {
          "label": "Logical 52.7MB",
          "value": (Math.floor(Math.random() * (100 - 10 + 1)) + 10)
        },
        {
          "label": "Physical 1.3 MB",
          "value": (Math.floor(Math.random() * (100 - 10 + 1)) + 10)
        }
      ]
    }
  ];
}



