const dataCenterWaizardRoutes=[
  { path:'datacenter',component:DATACENTERComponent ,
    children:[
        // { path:'dataStore_details',component:DataStoreDetailsComponent,outlet:'datacenterwizard_outlet'}
    ]
  }
]

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(dataCenterWaizardRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class DataCenterWizardRoutingModule {}



