import {Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import {PerformanceListViewConfig} from './performanceListView.component.config';
import {Router} from '@angular/router';
import {PostReq} from '../../services/post.service';



declare let d3: any;
@Component({
  selector: 'performanceList-component',
  styleUrls: ['./performanceChartDialog.component.scss'],
  templateUrl: './performanceListView.component.html',
  providers: [PostReq]
})

export class PerformanceListViewComponent  {

  private currentRoute: string;
  private data;
  private percentageWidth:number = 0;

  constructor(private route: Router, private postService: PostReq) {
    let _routePath: Array<string> = route.url.split('/'),
      currentRoute: string = _routePath[_routePath.length - 1];

    currentRoute = currentRoute.split('?')[0];

    if (PerformanceListViewConfig.hasOwnProperty(currentRoute)) {
      this.currentRoute = currentRoute;
      PerformanceListViewConfig[currentRoute].dataServiceProvider['payload'] = {'id': currentRoute};
      this.postService.send(PerformanceListViewConfig[currentRoute].dataServiceProvider).subscribe(
        data => {
          this.data = data;
          this.percentageWidth = data.hitRate || 0;
          this['lstData'] = Object.keys(data).map((d) => {
            return {k:d, value: data[d]};
          })
        },
        err => {
          this.data = [];
        }
      );
    }
  }


}



