import { Component, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService, LoaderService } from './services/index';

@Component({
  selector: 'my-app',
  template: `
    <section [ngClass]="{'htmlClient': isHtmlClient, 'flexClient': isHtmlClient == false}">
      <img alt="Tintri Logo" src="https://www.tintri.com/sites/all/themes/tt/logo.png" class="logo flRight" typeof="foaf:Image">                                                                                                                                     
      <section class="layout-content">
        <router-outlet></router-outlet>
      </section>
    </section>
    <div *ngIf="showLoader" class="loading"></div>
    <login-form (hidePopUp)="hideLoginForm($event)"  class="flLeft" [ngClass]="{'hide': (isAuthenticated || hideDialog || true)}" id="login-form" [shouldAuthenticated] = '!isAuthenticated'></login-form>
  `,
  providers: [AuthenticationService]
})

export class AppComponent implements AfterViewInit{
  private isAuthenticated:Boolean = true;
  private hideDialog:Boolean = false;
  private showLoader: boolean;
  private isHtmlClient = true;

  constructor(private router: Router, private service: AuthenticationService,  private loaderService: LoaderService){
    router.events.subscribe((val) => {
      this.isAuthenticated = this.service.checkCredentials();
    });
  }

  ngAfterViewInit() {
    this.isHtmlClient = window['WEB_PLATFORM'].getClientType() != 'flex';
  }

  hideLoginForm(event) {
    this.hideDialog = true;
  }

  ngOnInit(){
    this.loaderService.status.subscribe((val: boolean) => {
      this.showLoader = val;
    });
  }
}



