import {LoginComponent} from './components/index';

@NgModule({
  imports: [
    BrowserAnimationsModule,
    BrowserModule,
    FormsModule,
    CommonModule,
    HttpModule,
    SharedModule,
    DataCenterModule,
    ClusterModule,
    GLOBALModule,
    DataStoreModule,
    HostModule,
    SummaryModule,
    DataCenterWizardModule,
    AppRoutingModule
  ],
  declarations: [
    AppComponent, AppHeader, LoginComponent
  ],
  bootstrap: [AppComponent],
  providers: [
    BaseRequestOptions,
    LoaderService
  ],
  exports: [],
  entryComponents: [
    LoginComponent,
    MultiBarChartComponent,
    DataCenterWizardComponent,
    ProgressIndicator
  ]
})
export class AppModule {
}



