import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map'

import 'rxjs/add/operator/catch';
import 'rxjs/Rx';
//import {PostReq} from "./post.service";
import {GetReq} from './get.service';
import {UserProfile} from "../models/userProfile";
import {ServiceProvider} from "../models/serviceProvider";
import { Http } from '@angular/http';

@Injectable()
export class DataCenterService extends GetReq{



  getDeviceLst(userInfo: UserProfile, serviceProvider: ServiceProvider) {
    serviceProvider.payLoad = userInfo;
    return this.send(serviceProvider);
  }

  getVoumeLst(deviceInfo = {}, serviceProvider: ServiceProvider) {
    serviceProvider.payLoad = deviceInfo;
    return this.send(serviceProvider);
  }



}



