import {Directive, HostListener, OnInit, EventEmitter, ElementRef} from '@angular/core';


@Directive({ selector: '[wDraggable]' })
export class Draggable implements OnInit {

  private mouseup = new EventEmitter();
  private mousedown = new EventEmitter();
  private mousemove = new EventEmitter();
  private  mousedrag;

  @HostListener('document:mouseup', ['$event'])
  onMouseup(event) {
    this.mouseup.emit(event);
  }

  @HostListener('mousedown', ['$event'])
  onMousedown(event) {
    this.mousedown.emit(event);
    return false; // Call preventDefault() on the event
  }

  @HostListener('document:mousemove', ['$event'])
  onMousemove(event) {
    this.mousemove.emit(event);
  }

  constructor(public element: ElementRef) {
    this.element.nativeElement.classList.add('draggable');
    this.element.nativeElement.style.position = 'relative';
    this.element.nativeElement.style.cursor = 'pointer';

    this.mousedrag = this.mousedown.map(event => {
      return {
        top: event['clientY'] - this.element.nativeElement.getBoundingClientRect().top,
        left: event['clientX'] - this.element.nativeElement.getBoundingClientRect().left
      };
    })
      .flatMap(
        imageOffset => this.mousemove.map(pos => ({
          top: pos['clientY'] - imageOffset.top,
          left: pos['clientX'] - imageOffset.left
        }))
          .takeUntil(this.mouseup)
      );
  }

  ngOnInit() {
    this.mousedrag.subscribe({
      next: pos => {
        this.element.nativeElement.style.top = pos.top + 'px';
        this.element.nativeElement.style.left = pos.left + 'px';
      }
    });
  }
}




