import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map'

import {PostReq} from "./post.service";
import {ServiceProvider, loginInterface, UserProfile} from "../models/index";


@Injectable()
export class AuthenticationService extends PostReq{

  login(userInfo: loginInterface, serviceProvider: ServiceProvider) {
    serviceProvider.payLoad = userInfo;
    return this.send(serviceProvider);
  }

  getUserProfile() : UserProfile {
    return JSON.parse(sessionStorage.getItem("currentUser"));
  }

  logout() {
    // remove user from local storage to log user out
    sessionStorage.removeItem('currentUser');
  }

  checkCredentials() {
    return sessionStorage.getItem("currentUser") !== null;
  }
}



