import {ServiceProvider} from "../../models/index";

interface  ClusterConfigInterface {
  existingVolumes: ServiceProvider,
  targetNames: ServiceProvider,
  hostLst: ServiceProvider,
  NaaID: ServiceProvider,
  createNewZvol: ServiceProvider,
  createNewDataStore:ServiceProvider
}

export const DataStoreWizardConfig: ClusterConfigInterface = {
  existingVolumes: {
    url: 'https://172.30.36.223:9443/ui/tintristorage/rest/ttstorage/volumes',
    type: 'GET',
    contentType: 'application/json'
  },
  targetNames: {
    url: 'https://172.30.36.223:9443/ui/tintristorage/rest/ttstorage/targets',
    type: 'GET',
    contentType: 'application/json'
  },
  hostLst: {
    url: 'https://172.30.36.223:9443/ui/tintristorage/rest/ttstorage/hosts',
    type: 'GET',
    contentType: 'application/json'
  },
  NaaID: {
    url: 'https://172.30.36.223:9443/ui/tintristorage/rest/ttstorage/naa?hostname=${hostIP}',
    type: 'GET',
    contentType: 'application/json'
  },
  createNewZvol: {
    url: 'https://172.30.36.223:9443/ui/tintristorage/rest/ttstorage/extentvol?volumename=${volName}&&zvolname=${zVolName}&&zvolsize=${zVolSize}&&targetname=${targetName}',
    type: 'GET',
    contentType: 'application/json'
  },
  createNewDataStore:{
    url: 'https://win-rhq2-vc65.tintri.local.com/ui/tintristorage/rest/ttstorage/createvmfs?hostname=${hostName}&&naa=${naa}&&dsname=${dsName}',
    type: 'GET',
    contentType: 'application/json'
  }
};



