/**
 * Created by Venkatesh on 3/2/2017.
 * As per the best practices moved routing to separate module
 */

import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { RouterModule, Routes ,ActivatedRoute, Params }  from '@angular/router';
import {GLOBALComponent} from "./index";

const globalRoutes=[
  { path:'global',component:GLOBALComponent }
]

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(globalRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class GlobalRoutingModule {}



