import {Component, Input, Output, EventEmitter} from '@angular/core';
import {AuthenticationService} from '../../services/index';
import {loginInterface} from '../../models/index';
import {LoginComponentConfig} from './login.component.config';

@Component({
  selector: 'login-form',
  moduleId: module.id,
  templateUrl: 'login.component.html',
  providers: [AuthenticationService],
  styleUrls: ['login.component.scss']
})
export class LoginComponent {

  @Input() shouldAuthenticated: Boolean = true;
  @Output() hidePopUp: EventEmitter<boolean> = new EventEmitter<boolean>();

  private authHeader: String = "Discover K2 Devices";

  private hostCredentials: loginInterface = {
    shouldCheckSSL: true,
    hostName: 'xxxx',
    userName: 'x1234',
    password: 'xx1234'
  }

  private userProfile = {}

  constructor(private  _service:AuthenticationService) {
    this.shouldAuthenticated = _service.checkCredentials();
    this.shouldAuthenticated = true;
  }

  authenticateUser(credentials) {
    this._service.login(credentials, LoginComponentConfig.serviceProvider)
      .subscribe(
        user => {
              if (user && user.token) {
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                sessionStorage.setItem('currentUser', JSON.stringify(user));
                console.log("Authenticated ")
              }
        },
        err => {
          console.log(err);
        }
      );
  }

  closeDialog() {
    this.hidePopUp.emit(true);
  }

}



