import {ServiceProvider} from "../../models/index";

interface  AreaChartInterface {
  dataServiceProvider: ServiceProvider;
}

export const AreaChartConfig = {
  throughOP: {
    dataServiceProvider: {
      url: '/api/getAreaChartDummyThroughOP',
      type: 'POST',
      contentType: 'application/json'
    },
    'color': 'rgb(31, 119, 180)'
  },
  iOP: {
    dataServiceProvider: {
      url: '/api/getAreaChartDummyiOP',
      type: 'POST',
      contentType: 'application/json'
    },
    'color': 'rgb(255, 127, 14)'
  }
}



