import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';

import {RouterModule}  from '@angular/router';
import {SUMMARYComponent} from "./summary.component";
import {AreaChartComponent} from '../../components/AreaChart/areaChart.component';
import {PerformanceListViewComponent} from '../../components/PerformanceListView/performanceListView.component';

const summaryRoutes = [
  {
    path: 'summary-portlet', component: SUMMARYComponent,
    children: [
      {
        path: 'throughOP',
        component: AreaChartComponent,
      },
      {
        path: 'iOP',
        component: AreaChartComponent,
      },
      {
        path: 'tintriPerf',
        component: PerformanceListViewComponent,
      }
    ]
  }
]



@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(summaryRoutes)
  ],
  declarations: [
  ],
  exports: [
    RouterModule
  ]
})
export class SummaryRoutingModule {
}



