import {Component, Input} from '@angular/core';

@Component({
  selector: 'tree',
  moduleId: module.id,
  templateUrl: './tree.component.html',
  styleUrls: ['./tree.component.scss']
})
export class TreeComponent {

  @Input() data: any[];
  @Input() showCheckBox: boolean;
  toggle(el) {
    el.isExpanded = !el.isExpanded;
  }
}



