import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import {AgGridModule} from "ag-grid-angular/main";
import { DataCenterService } from "../../services/datacenter.service";
import { DATACENTERComponent, DATASTOREComponent, CLUSTERComponent, GLOBALComponent, HOSTComponent} from '../index';
import { BarChartComponent, MultiBarChartComponent, PieChartComponent, AreaChartComponent, TabComponent, Tabs, PerformanceChartDialogComponent } from '../../components/index';
import { nvD3 } from '../../helper/ng2-nvd3';
import {DataCenterWizardComponent} from "../../components/DataCenterWizard/datacenterwizard.component";
import {ProgressIndicator} from "../../components/ProgressIndicator/progressindicator.component";
import {ConnectedHostComponent} from "../../components/ConnectedHost/connectedHost.component";
import {TreeComponent} from "../../components/TreeComponent/tree.component";
import {TreeNodeComponent} from "../../components/TreeNode/treeNode.component";
import {PerformanceListViewComponent} from "../../components/PerformanceListView/performanceListView.component";
import { FormsModule  } from '@angular/forms';
import {Draggable} from '../../directives/draggable';
import {ESCClose} from '../../directives/closeOnESC';

import {MdSliderModule} from '@angular/material';

@NgModule({
  imports: [
    BrowserModule,FormsModule,
    MdSliderModule,
    AgGridModule.withComponents([DATACENTERComponent,GLOBALComponent,CLUSTERComponent, DATASTOREComponent, HOSTComponent])
  ],
  exports :[
    nvD3,
    Draggable,
    ESCClose,
    DATACENTERComponent,
    DATASTOREComponent,
    HOSTComponent,
    AreaChartComponent,
    BarChartComponent,
    MultiBarChartComponent,
    PieChartComponent,
    TabComponent,
    Tabs,
    PerformanceChartDialogComponent,
    DataCenterWizardComponent,
    ProgressIndicator,
    DataCenterWizardComponent,
    ConnectedHostComponent,
    TreeComponent,
    TreeNodeComponent,
    PerformanceListViewComponent
  ],
  declarations: [
    nvD3,
    Draggable,
    ESCClose,
    DATACENTERComponent,
    DATASTOREComponent,
    AreaChartComponent,
    HOSTComponent,
    BarChartComponent,
    MultiBarChartComponent,
    PieChartComponent,
    TabComponent,
    Tabs,
    PerformanceChartDialogComponent,
    DataCenterWizardComponent,
    ProgressIndicator,
    PerformanceListViewComponent,
    DataCenterWizardComponent,
    ConnectedHostComponent,
    TreeComponent,
    TreeNodeComponent
  ],
  bootstrap: [],
  providers: [DataCenterService]
})
export class SharedModule {
}




