import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { DATASTOREComponent, DataStoreRoutingModule } from "./index";
import {AgGridModule} from "ag-grid-angular/main";
import { DataCenterService } from "../../services/datacenter.service";
import { SharedModule } from "../shared-module/shared.module";

@NgModule({
  imports: [
    BrowserModule,
    SharedModule,
    DataStoreRoutingModule,
    AgGridModule.withComponents(DATASTOREComponent)
  ],
  exports :[ ],
  declarations: [ ],
  bootstrap: [],
  providers: [DataCenterService]
})
export class DataStoreModule {
}




