import {Component, Output, EventEmitter, OnInit, ElementRef} from '@angular/core';
import {GridOptions} from "ag-grid";
import {DataStoreWizardConfig} from './datacenterwizard.component.config';
import {DataStoreWizardService} from '../../services/createDataStore.service';

@Component({
  selector: 'datacenterWizard-component',
  templateUrl: './datacenterwizard.component.html',
  styleUrls: ['./datacenterwizard.component.scss'],
  providers:[DataStoreWizardService]
})
export class DataCenterWizardComponent implements OnInit {

  @Output() navToWizard: EventEmitter<any> = new EventEmitter<any>();
  @Output() previousStep:EventEmitter<any> = new EventEmitter<any>();
  @Output() nextWizStep:EventEmitter<any> = new EventEmitter<any>();
  @Output() closeDataStoreWizard:EventEmitter<boolean> = new EventEmitter<boolean>();

  private successMsgFlag=false;
  //The data has to be moved to configuration
  private wizardConfig= {
    wizard_path: [{'path': 'dataStore_details','wizard':'DataStore Details','step':1},
      // {'path': 'host_selection','wizard':'Host Selection','step':2},
      {'path': 'volume_configuration','wizard':'Volume Configuration','step':2},
          // {'path': 'volume_group_configuration','wizard':'Volume Group Configuration','step':4},
            {'path': 'summary','wizard':'Summary','step':3}],
        dataStoreDetails: {
          dataStoreName: '',
          memoryUnitsSize: '',
          selectedMemorySize: 1,
          blockSize: [512, 1024, 2048, 4096],
          memoryUnityType: ['TB','GB','MB'],
          vmfsVersion: ['VMFS5','VMFS4','VMFS3']
        },
        selectedPathId:1,
        headerLbl:"",
        hostSelection: {
          showCheckBox: true,
          dataLst:[{
            label: 'WIN-RHQ2-VC65.tintri.local.com',
            isExpanded: true,
            selected: true,
            subs: [
              {
            label: '172.30.37.58',
            isExpanded: false,
            selected: true
          }
        ]
    }]
    },
    volumeConfig: {
      isNewVolumeSelected: true,
      isExistingVolumeSelected: false,
      volumeNames: '',
      targetNames: '',
      createdNAAId: '',
      volumeGridOptions: <GridOptions>{
        columnDefs: [
          {headerName: "Name", field: "volumeName"},
          {headerName: "IQN", field: "iqn"},
          {headerName: "Capacity", field: "capacity"},
          {headerName: "Dedup", field: "dedup"}
        ],
        rowData: null, enableRowSelection: true,
        rowHeight: 35,
        enableColResize: true,
        enableSorting: true,
        rowSelection: 'single',
        onGridReady: () => {
          this.wizardConfig.volumeConfig.volumeGridOptions.api.sizeColumnsToFit();
        }
      }
    },
    volumeGroupConfig: {
      isNewVolumeSelected: false,
      isExistingVolumeSelected: true,
      volumeGroup:''
    }
  };

  private selectedValueFromWizard;

  constructor(private service: DataStoreWizardService, myElement: ElementRef){
  }

  initModel(){
    this.selectedValueFromWizard={
      dsName:'',
      dsSize:'',
      memUnitType:'',
      VMFSVal:'',
      blockVer:'',
      volumeVal:'',
      targetVal:'',
      selectedHost:'172.30.37.58',
      zvol:''
    };
  }

  ngOnInit(){
    this.successMsgFlag=false;
    this.navigateToWizard(0);
    this.loadData();
    this.initModel();
  }

  loadData() {
    this.service.getVolumeLst(DataStoreWizardConfig.existingVolumes).subscribe(data => {this.wizardConfig.volumeConfig.volumeNames = data}, err => {console.log(err)});
    this.service.getTargetLst(DataStoreWizardConfig.targetNames).subscribe(data => {this.wizardConfig.volumeConfig.targetNames = data}, err => {console.log(err)});
    this.service.getHostLst(DataStoreWizardConfig.hostLst).subscribe(data => {this.wizardConfig.hostSelection.dataLst[0].subs = data.map((d) => {return {
      label: d,
      isExpanded: false,
      selected: false
    }})}, err => {console.log(err)});
    // this.service.getNAALst(DataStoreWizardConfig.NaaID).subscribe(data => {console.log(data)}, err => {console.log(err)});
  }

  createNewVol () {
    let serviceUrl = DataStoreWizardConfig.createNewZvol.url;
    DataStoreWizardConfig.createNewZvol.url = serviceUrl.replace('${volName}', this.selectedValueFromWizard.volumeVal)
                    .replace('${zVolName}', this.selectedValueFromWizard.zvol)
                    .replace('${zVolSize}', (parseInt(this.selectedValueFromWizard.dsSize) || 1) + "")
                    .replace('${targetName}', this.selectedValueFromWizard.targetVal);

    return this.service.createNewZVol(DataStoreWizardConfig.createNewZvol);
  }

  createNewDataStore(){
    let serviceUrl = DataStoreWizardConfig.createNewDataStore.url;

    DataStoreWizardConfig.createNewDataStore.url = serviceUrl.replace('${hostName}', this.selectedValueFromWizard.selectedHost)
      .replace('${naa}', this.wizardConfig.volumeConfig.createdNAAId)
      .replace('${dsName}', this.selectedValueFromWizard.dsName)

    return this.service.createNewDataStore(DataStoreWizardConfig.createNewDataStore);
  }

  navigateToWizard(step:any){
    this.wizardConfig.selectedPathId=step;
    this.wizardConfig.headerLbl=this.wizardConfig.wizard_path[step].wizard;
  }

  nextStep(){

    if(this.wizardConfig.selectedPathId === 2) {
      this.createNewVol().subscribe(
        data => {
          console.log("NAA" + data);
          this.wizardConfig.volumeConfig.createdNAAId = data.naa;
          this.wizardConfig.selectedPathId++;
          this.navigateToWizard(3);
        },
        err => {
          console.log(err);
          alert("volume creation failed;");
        });
    }
    else if(this.wizardConfig.selectedPathId === 4 ? this.wizardConfig.selectedPathId = 0 : this.wizardConfig.selectedPathId++)
        this.navigateToWizard(this.wizardConfig.selectedPathId);

  }

  prevStep(){
    if(this.wizardConfig.selectedPathId === 0 ? this.wizardConfig.selectedPathId = 4 : this.wizardConfig.selectedPathId--)
      this.navigateToWizard(this.wizardConfig.selectedPathId)
  }

  closeWizard(){
    this.selectedValueFromWizard
    this.closeDataStoreWizard.emit(false);
    this.initModel();
    this.successMsgFlag=false;
  }

  volumeTypeToggleClick(name) {
    if(name === "newVolumeConfig") {
      this.wizardConfig.volumeConfig.isNewVolumeSelected = true;
      this.wizardConfig.volumeConfig.isExistingVolumeSelected = false;
    }
    else {
      this.wizardConfig.volumeConfig.isExistingVolumeSelected = true;
      this.wizardConfig.volumeConfig.isNewVolumeSelected = false;
    }
  }

  onMemUnitChange(data){
    this.selectedValueFromWizard.memUnitType=data;
  }

  onVMFSChange(data){
    this.selectedValueFromWizard.VMFSVal=data;
  }

  onBlockVerChange(data){
    this.selectedValueFromWizard.blockVer=data;
  }

  getVolmeChange(data){
    this.selectedValueFromWizard.volumeVal = data;
  }

  getTargetChange(data){
    console.log(data);
    this.selectedValueFromWizard.targetVal = data;
  }


  finishWizard(event){
    this.createNewDataStore().subscribe(
      data => {
        this.successMsgFlag=true;
        console.log(data);
        alert("Data Store Created");
      },
      err => {
        alert("volume creation failed; " + JSON.stringify(err))
      });
  }


}



