import {Component, OnInit, Input, AfterViewInit} from '@angular/core';
import {nvD3} from 'ng2-nvd3';
import {AgRendererComponent} from 'ag-grid-angular/main';

declare let d3: any;

@Component({
  selector: 'multiBarChart-component',
  template: `<div style="width:100%;height: inherit">
          <nvd3 [options]="options" [data]="data"></nvd3>
  </div>`,
  providers: []
})

export class MultiBarChartComponent implements AgRendererComponent {
  options;
  @Input() height;
  @Input() data;

  ngOnInit() {
    this.renderData();
  }

  agInit(params:any):void {
    this.renderData()
  }

  renderData(){
    this.options = <nvD3>{
      "chart": {
        "type": "multiBarHorizontalChart",
        "height": this.height,
        "width": 800,
        "showControls": false,
        "showLegend": false,
        "showValues": true,
        "duration": 250,
        x: (d) => d.label,
        y: (d) => d.value,
        showYAxis: false,
        "margin": {
          "top": 10,
          "right": 100,
          "bottom": 10,
          "left": 100
        },
        "xAxis": {
          "showMaxMin": false
        },
        "yAxis": {
          "axisLabel": "Values"
        }
      }
    };
  }
}



