import {Component, OnInit, Input} from '@angular/core';
import {nvD3} from 'ng2-nvd3';
import {Router, ActivatedRoute} from '@angular/router';
import {AreaChartConfig} from './areaChart.component.config';
import {PostReq} from '../../services/post.service';
declare let d3: any;
@Component({
  selector: 'areachart-component',
  templateUrl: './areaChart.component.html',
  styleUrls: ['./areaChart.component.scss'],
  providers: [PostReq]
})

export class AreaChartComponent implements OnInit {
  options;
  currentRoute: string;

  @Input() title;
  @Input() height;
  @Input() data;

  constructor(private route: Router, private postService: PostReq) {
    let _routePath: Array<string> = route.url.split('/'),
      currentRoute: string = _routePath[_routePath.length - 1];

    currentRoute = currentRoute.split('?')[0];

    if (AreaChartConfig.hasOwnProperty(currentRoute)) {
      this.currentRoute = currentRoute;
      AreaChartConfig[currentRoute].dataServiceProvider['payload'] = {'id': currentRoute};
      this.postService.send(AreaChartConfig[currentRoute].dataServiceProvider).subscribe(
        data => {
          this.data = data;
          if (this.data.length > 0)
            this.data[0].color = AreaChartConfig[currentRoute].color;
        },
        err => {
          this.data = [];
        }
      );
    }
  }

  ngOnInit() {
    this.options = <nvD3>{
      "chart": {
        "type": "stackedAreaChart",
        "height": this.height,
        "margin": {
          "top": 30,
          "right": 20,
          "bottom": 30,
          "left": 40
        },
        legend: {
          "top": 5,
          "right": 0,
          "bottom": 10,
          "left": 0
        },
        "title": {
          "enable": true,
          "text": this.title,
          "className": "h4",
          "css": {
            "textAlign": "left"
          }
        },
        x: function (d) {
          return d[0];
        },
        y: function (d) {
          return d[1];
        },
        showControls: false,
        "useVoronoi": false,
        "clipEdge": true,
        "duration": 250,
        "useInteractiveGuideline": true,
        "xAxis": {
          "showMaxMin": false,
          "tickFormat": (d) => d3.time.format('%x')(new Date(d))
        },
        "yAxis": {
          "tickFormat": (d) => d3.format(',.2f')(d)
        },
        "zoom": {
          "enabled": false,
          "scaleExtent": [
            1,
            10
          ],
          "useFixedDomain": false,
          "useNiceScale": false,
          "horizontalOff": true,
          "verticalOff": true
        }
      }
    };
    if (!this.data)
      this.data = [];
  }
}



