import { Component,OnInit } from '@angular/core';
import {GridOptions} from "ag-grid";
import {ClusterConfig, deviceColumnDefs, volumeColumnDefs} from './cluster.component.config';
import {ClusterService, AuthenticationService} from "../../services/index";
import {MultiBarChartComponent, DataCenterWizardComponent} from "../../components/index";
import {Router} from '@angular/router';
import {_tmpMultiAreaChartDatafn} from '../../models/index';
import {DateUtil} from "../../services/date-util.service";


@Component({
  selector: 'cluster-component',
  templateUrl: 'cluster.component.html',
  styleUrls : ['cluster.component.scss'],
  providers: [ClusterService,DateUtil]
})
export class CLUSTERComponent implements OnInit{

  private _timeStamp:any = '';
  private clusterGridOptions: GridOptions;
  private volumeGridOptions: GridOptions;
  private showHostDialog: boolean = false;
  private showPerformanceDialog: boolean = false;
  private _tmpAreaChartData = _tmpMultiAreaChartDatafn();


  constructor(private auth:AuthenticationService, private service:ClusterService,private router:Router,private dateUtil:DateUtil) {
  }

  onDeviceLstCellClicked(event){
    console.log(event);
    if (event.colDef.field == "performance") {
      this.showPerformanceDialog = true;
      this._tmpAreaChartData = _tmpMultiAreaChartDatafn();
    }
    else if(event.colDef.field == "host") {
      this.showHostDialog = true;
    }
  }

  closePerformanceDialog(shodeClose) {
    this.showPerformanceDialog = false;
  }

  hideConnectedHost() {
    this.showHostDialog = false;
  }

  ngOnInit() {
    console.log(this);
    this._timeStamp = this.dateUtil.getCurrentDateAndTime()
    this.clusterGridOptions = <GridOptions><GridOptions>{
      columnDefs: deviceColumnDefs,
      rowData: null,
      rowSelection: 'single',
      suppressRowClickSelection: true,
      enableColResize: true,
      enableSorting: true,
      minRowsToShow: 1,
      rowHeight: 35,
      onGridReady: () => {
        this.clusterGridOptions.api.sizeColumnsToFit();
      }
    };
    this.volumeGridOptions = <GridOptions><GridOptions>{
      columnDefs: volumeColumnDefs,
      rowData: null,
      rowSelection: 'multiple',
      suppressRowClickSelection: true,
      enableSorting: true,
      enableColResize: true,
      minRowsToShow: 1,
      rowHeight: 35,
      onGridReady: () => {
        this.volumeGridOptions.api.sizeColumnsToFit();
      }
    };

    this.loadDeviceLst();
    this.loadVolumeInfo({uuid:45345}); //TODO remove after fix first row click issue
  }

  loadDeviceLst() {
    this.service.getClusterDeviceLst(this.auth.getUserProfile(), ClusterConfig.deviceServiceProvider)
      .subscribe(
        data => {
          this.clusterGridOptions.rowData = data;
        },
        err => {
          this.clusterGridOptions.rowData = [];
        }
      );
  }


  loadVolumeInfo(data) {
    this.service.getVoumeLst(data, ClusterConfig.volumeServiceProvider)
      .subscribe(
        data => {
          this.volumeGridOptions.rowData = data;
        },
        err => {
          this.volumeGridOptions.rowData = [];
          console.log(err);
        }
      );
  }
}



