import {Directive, HostListener, OnInit, EventEmitter, ElementRef, Input, HostBinding} from '@angular/core';

@Directive({ selector: '[closeOnEsc]' })
export class ESCClose implements OnInit {

  // @HostBinding('closeDialog')
  @Input() closeDialog;

  @HostListener('document:keydown', ['$event'])
  onKeyPress(event) {
    console.log(event);
    this.closeDialog();
  }

  constructor(public element: ElementRef) {
  }

  ngOnInit() {
  }
}




