import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/Rx';
import {ServiceProvider} from '../models/serviceProvider';
import {GetReq} from './get.service';


@Injectable()
export class DataStoreWizardService extends GetReq{

  getVolumeLst(serviceProvider: ServiceProvider) {
    return this.send(serviceProvider);
  }

  getTargetLst(serviceProvider: ServiceProvider) {
    return this.send(serviceProvider);
  }

  getHostLst(serviceProvider: ServiceProvider) {
    return this.send(serviceProvider);
  }

  getNAALst(serviceProvider: ServiceProvider) {
    return this.send(serviceProvider);
  }

  createNewZVol(serviceProvider: ServiceProvider) {
    return this.send(serviceProvider);
  }

  createNewDataStore(serviceProvider: ServiceProvider) {
    return this.send(serviceProvider);
  }

}



