import {Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {nvD3} from 'ng2-nvd3';

import {_tmpMultiAreaChartDatafn} from '../../models/index'
declare let d3: any;
@Component({
  selector: 'performanceDialog-component',
  styleUrls: ['./performanceChartDialog.component.scss'],
  templateUrl: './performanceChartDialog.component.html',
  providers: []
})

export class PerformanceChartDialogComponent implements OnInit {
  options;

  @Input() headerTxt: string;
  @Input() showDialog: boolean;
  @Input() height:number;
  @Input() title:string;
  @Input() data;

  @Output() dialogClose: EventEmitter<boolean> = new EventEmitter<boolean>();


  ngOnInit() {
    this.options = <nvD3>{
      "chart": {
        "type": "stackedAreaChart",
        "height": this.height,
        "margin": {
          "top": 30,
          "right": 20,
          "bottom": 30,
          "left": 40
        },
        legend:{
          "top": 5,
          "right": 0,
          "bottom": 10,
          "left": 0
        },
        "title": {
          "enable": true,
          "text": this.title,
          "className": "h4",
          "css": {
            "textAlign": "left"
          }
        },
        x: function(d){return d[0];},
        y: function(d){return d[1];},
        showControls: false,
        "useVoronoi": false,
        "clipEdge": true,
        "duration": 100,
        "useInteractiveGuideline": true,
        "xAxis": {
          "showMaxMin": false,
          "tickFormat": (d) => d3.time.format('%x')(new Date(d))
        },
        "yAxis": {
          "tickFormat": (d) => d3.format(',.2f')(d)
        },
        "zoom": {
          "enabled": false,
          "scaleExtent": [
            1,
            10
          ],
          "useFixedDomain": false,
          "useNiceScale": false,
          "horizontalOff": true,
          "verticalOff": true
        }
      }
    };
  }

  refreshAreaChart(val) {
    this.data = _tmpMultiAreaChartDatafn();
  }

  closeDialogEvent() {
    this.dialogClose.emit(true);
  }
}



