import {Component, Input} from '@angular/core';

@Component({
  selector: 'node',
  moduleId: module.id,
  templateUrl: 'treeNode.component.html',
  styleUrls: ['treeNode.component.scss']
})
export class TreeNodeComponent {

  @Input() item: any;
  @Input() showCheckBox: boolean;
  toggle(el) {
    el.isExpanded = !el.isExpanded;
  }

  toggleSelection(items, isChecked) {
    items.forEach((d)=>{
      d.selected = isChecked;
      if(d.hasOwnProperty("subs")) {
        this.toggleSelection(d.subs, isChecked);
      }
    });
  }

  checkBoxOnChange(e, d) {
    d['selected'] = e.checked || false;
    if(d.hasOwnProperty("subs")) {
      this.toggleSelection(d.subs, d.selected);
    }
  }
}



