import {Component, OnInit, Input } from '@angular/core';
import {nvD3} from 'ng2-nvd3';

declare let d3: any;

@Component({
  selector: 'piechart-component',
  template: `<div style="width:100%;height: 100%">
          <nvd3 [options]="options" [data]="data"></nvd3>
  </div>`,
  providers: []
})

export class PieChartComponent implements OnInit {
  options;

  @Input() height;
  @Input() data;

  ngOnInit() {
    this.options = <nvD3>{
      chart: {
        type: 'pieChart',
        height: this.height,
        x: function (d) {
          return d.key;
        },
        y: function (d) {
          return d.value;
        },
        showValues: false,
        valueFormat: function (d) {
          return d;
        },
        duration: 500,
        legend: {
          margin: {
            top: 5,
            right: 0,
            bottom: 0,
            left: 0
          }
        }
      }
    };
  }
}



